/* fonts  */

#container-s {
  box-shadow: 0 15px 30px 1px rgba(128, 128, 128, 0.31);
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  margin: 5em auto;
  height: 50vh;
  width: 70%;
}

/* 	Product details  */
.product-details-s {
  position: relative;
  text-align: left;
  overflow: hidden;
  padding: 30px;
  height: 100%;
  float: left;
  width: 40%;
}

/* 	Product Name */
#container-s .product-details-s h1 {
  font-family: 'Old Standard TT', serif;
  display: inline-block;
  position: relative;
  font-size: 34px;
  color: #344055;
  margin: 0;
}

#container-s .product-details-s h1:before {
  position: absolute;
  content: '';
  right: 0%;
  top: 0%;
  -webkit-transform: translate(25px, -15px);
          transform: translate(25px, -15px);
  font-family: 'Farsan', cursive;
  display: inline-block;
  background: #ffa69e;
  border-radius: 5px;
  font-size: 14px;
  padding: 5px;
  color: #fff;
  margin: 0;
  -webkit-animation: chan-sh 6s ease infinite;
          animation: chan-sh 6s ease infinite;
}

@-webkit-keyframes chan-sh {
  from {
    content: 'free shipping';
  }
  to {
    content: 'New';
  }
}

@keyframes chan-sh {
  from {
    content: 'free shipping';
  }
  to {
    content: 'New';
  }
}

/*Product Rating  */
.hint-star {
  display: inline-block;
  margin-left: 0.5em;
  color: gold;
  width: 50%;
}

/* The most important info-srmation about the product */
#container-s .product-details-s > p {
  font-family: 'Farsan', cursive;
  text-align: center;
  font-size: 20px;
  color: #7d7d7d;
}

/* control */

.control {
  position: absolute;
  bottom: 20%;
  left: 22.8%;
}
/* the Button */
.btn-s {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  background-color: steelblue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.btn-s:hover {
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}

.btn-s span {
  font-family: 'Farsan', cursive;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  display: inline-block;
  padding: 10px 20px;
  font-size: 1.2em;
  margin: 0;
}
/* shopping cart icon */
.btn-s .price,
.shopping-cart {
  background: #333;
  border: 0;
  margin: 0;
}

.btn-s .price {
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
  padding-right: 15px;
}

/* the Icon */
.btn-s .shopping-cart {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  position: absolute;
  background: #333;
  z-index: 1;
  left: 0;
  top: 0;
}

/* buy */
.btn-s .buy {
  z-index: 3;
  font-weight: bolder;
}

.btn-s:hover .price {
  -webkit-transform: translateX(110%);
          transform: translateX(110%);
}

.btn-s:hover .shopping-cart {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

/* product image  */
.product-image-s {
  transition: all 0.3s ease-out;
  display: inline-block;
  position: relative;
  overflow: hidden;
  height: 100%;
  float: right;
  width: 50%;
  display: inline-block;
}

#container-s img {
  width: 100%;
  height: 100%;
}

.info-s {
  background: rgba(27, 26, 26, 0.9);
  font-family: 'Farsan', cursive;
  transition: all 0.3s ease-out;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  position: absolute;
  line-height: 1.9;
  text-align: left;
  font-size: 120%;
  cursor: no-drop;
  color: #fff;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.info-s h2 {
  text-align: center;
}
.product-image-s:hover .info-s {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.info-s ul li {
  transition: 0.3s ease;
}
.info-s ul li:hover {
  -webkit-transform: translateX(50px) scale(1.3);
          transform: translateX(50px) scale(1.3);
}

.product-image-s:hover img {
  transition: all 0.3s ease-out;
}
.product-image-s:hover img {
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
}

